// src/pages/DashboardPage.js
import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { useTestData } from '../context/TestDataContext';
import { useTimetable } from '../context/TimetableContext';
import { useFocusItems } from '../context/FocusItemsContext';
import Greeting from '../components/Greeting';
import { AnimatePresence, motion } from 'framer-motion';
import { useHandlers } from '../context/HandlerContext';
import FocusItemCard from '../components/FocusItemCard';
import LoadingSpinner from '../components/LoadingSpinner';
import CircularProgressBar from '../components/CircularProgressBar';
import WeeklyTaskSummary from '../components/WeeklyTaskSummary';
import EditFocusModal from '../components/EditFocusModal';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  TouchSensor,
  useSensor,
  useSensors,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';

const DashboardPage = React.memo(({
  onNavigate,
  userName,
  isLoadingTodayFocus,
  currentTimetableSubject,
  setCurrentTimetableSubject,
  setCurrentSelectedSubject,
  navigateToTrophyPage,
}) => {
  const handlers = useHandlers();
  // Use context for test data and related state
  const { testData } = useTestData();
  const { timetableEntries } = useTimetable();
  const { todayFocusItems } = useFocusItems();

  const [isEditFocusModalOpen, setIsEditFocusModalOpen] = useState(false);

    // Copilot: Memoize subjects array from testData
    const subjects = useMemo(() => Object.keys(testData), [testData]);

    // Copilot: Memoize completedCount and totalCount for currentTimetableSubject
    const completedCount = useMemo(() => (
        testData[currentTimetableSubject]
            ? testData[currentTimetableSubject].filter(test => test.completed).length
            : 0
    ), [testData, currentTimetableSubject]);

    const totalCount = useMemo(() => (
        testData[currentTimetableSubject]
            ? testData[currentTimetableSubject].length
            : 0
    ), [testData, currentTimetableSubject]);

    // Copilot: Memoize progressPercentage calculation
    const progressPercentage = useMemo(() => {
        const subjectTests = testData[currentTimetableSubject] || [];
        const totalTests = subjectTests.length;
        const completedTests = subjectTests.filter(test => test.completed).length;
        return totalTests > 0 ? (completedTests / totalTests) * 100 : 0;
    }, [testData, currentTimetableSubject]);

    // Copilot: Memoize weeklyTaskData calculation
    const weeklyTaskData = useMemo(() => {
        if (!timetableEntries) return [];
        const days = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];
        const weeklySummary = [];
        const today = new Date();
        const todayDateString = today.toLocaleDateString('en-CA');
        const startOfWeek = new Date(today);
        startOfWeek.setHours(0, 0, 0, 0);
        startOfWeek.setDate(today.getDate() - today.getDay());

        for (let i = 0; i < 7; i++) {
            const currentDay = new Date(startOfWeek);
            currentDay.setDate(startOfWeek.getDate() + i);
            const dateString = currentDay.toLocaleDateString('en-CA');
            const tasksForDay = timetableEntries.filter(entry => entry.date === dateString);
            let completed = 0;
            let missed = 0;
            const now = new Date();
            tasksForDay.forEach(task => {
                if (task.checked) {
                    completed++;
                } else {
                    const eventDateTime = new Date(`${task.date}T${task.time}`);
                    if (eventDateTime < now) missed++;
                }
            });
            weeklySummary.push({ day: days[currentDay.getDay()], planned: tasksForDay.length, completed, missed, isToday: dateString === todayDateString });
        }
        return weeklySummary;
    }, [timetableEntries]);

    // Calculate today's date string in 'YYYY-MM-DD' format
    const todayDateString = useMemo(() => {
        const today = new Date();
        const year = today.getFullYear();
        const month = (today.getMonth() + 1).toString().padStart(2, '0');
        const day = today.getDate().toString().padStart(2, '0');
        return `${year}-${month}-${day}`;
    }, []);

    // Filter today's focus items for today (if they have a date field)
    const todaysFocusTests = useMemo(() => {
        return todayFocusItems.sort((a, b) => (a.time || '').localeCompare(b.time || ''));
    }, [todayFocusItems]);

    // Find the current ongoing or next upcoming focus test
    const currentFocusTest = useMemo(() => {
        const now = new Date();
        let ongoing = null;
        for (let i = 0; i < todaysFocusTests.length; i++) {
            const item = todaysFocusTests[i];
            if (!item.time) continue;
            const [hour, minute] = item.time.split(':').map(Number);
            const eventDate = new Date(now.getFullYear(), now.getMonth(), now.getDate(), hour, minute);
            if (eventDate <= now) {
                ongoing = item; // latest not in the future
            } else if (!ongoing) {
                // If all are in the future, pick the first one
                return item;
            }
        }
        return ongoing || null;
    }, [todaysFocusTests]);

    // Enhanced Timetable Event Status Logic
    const now = new Date();
    const sortedTimetable = [...timetableEntries].sort((a, b) => {
        const aTime = new Date(`${a.date}T${a.time}`);
        const bTime = new Date(`${b.date}T${b.time}`);
        return aTime - bTime;
    });

    // Utility: calculate event end time (default 1 hour duration if not specified)
    const getEventEndTime = (event) => {
        if (event.endTime) {
            return new Date(`${event.date}T${event.endTime}`);
        }
        const [hour, minute] = event.time.split(':').map(Number);
        return new Date(new Date(`${event.date}T${event.time}`).setHours(hour + 1));
    };

    let liveEvent = null;
    let upcomingEvent = null;
    let missedEvents = [];

    sortedTimetable.forEach((event, idx, arr) => {
        if (event.checked) return;
        const eventStart = new Date(`${event.date}T${event.time}`);
        const eventEnd = getEventEndTime(event);
        const nextEvent = arr[idx + 1];
        const nextStart = nextEvent ? new Date(`${nextEvent.date}T${nextEvent.time}`) : null;

        if (!liveEvent && now >= eventStart && (!nextStart || now < nextStart)) {
            liveEvent = event;
        } else if (!upcomingEvent && now < eventStart) {
            upcomingEvent = event;
        } else if (
            now > eventEnd && nextEvent && now > nextStart && !event.checked
        ) {
            missedEvents.push(event);
        }
    });


    const sensors = useSensors(
        useSensor(PointerSensor, { activationConstraint: { distance: 5 } }),
        useSensor(TouchSensor, { activationConstraint: { delay: 100, tolerance: 5 } }),
        useSensor(KeyboardSensor)
    );

    const handleDragEnd = (event) => {
        const { active, over } = event;
        if (active.id !== over.id) {
            const oldIndex = todayFocusItems.findIndex(item => item.id === active.id);
            const newIndex = todayFocusItems.findIndex(item => item.id === over.id);
            const newItems = arrayMove(todayFocusItems, oldIndex, newIndex);
            handlers.reorderFocusItems(newItems);
        }
    };

    const handleRemoveFocusItem = useCallback((id) => {
        handlers.removeFocusItem(id);
    }, [handlers]);

    const handleToggleTestCompletion = useCallback((id) => {
        handlers.toggleTestCompletion(id);
    }, [handlers]);

    const handleAddFocusItem = useCallback((item) => {
        handlers.addFocusItem(item);
    }, [handlers]);

    const handleCheck = useCallback((id) => {
        handlers.toggleTestCompletion(id);
    }, [handlers]);

    return (
        <>
            <div id="page-dashboard" className="page px-6 py-8 overflow-x-hidden" style={{overflow:'hidden'}} >
                
                <div
                    className="flex items-center gap-4 mb-8 cursor-pointer transition-colors duration-300"
                    onClick={() => onNavigate('profile')}
                >
                    <img src="https://placehold.co/80x80/E2E8F0/4A5568?text=NK" alt="User Avatar" className="w-16 h-16 rounded-full" />
                    <div>
                        <h2 className="text-2xl text-gray-600">Hello <span className="font-bold text-gray-800">Nikhil !</span></h2>
                        <Greeting userName={userName || 'User'} />
                    </div>
                </div>

                {/* Timetable Status Cards Section */}
                <div className="mb-4">
                  <h3 className="text-lg font-bold text-gray-800 mb-3 pl-1">Today's Timetable Status</h3>
                </div>
                <div className="mb-8">
                  <div
                    className="flex flex-nowrap overflow-x-auto pb-2 hide-scrollbar scroll-smooth w-full max-w-full"
                    style={{ WebkitOverflowScrolling: 'touch' }}
                  >
                    {/* Live Event Card */}
                    {liveEvent && (
                      <div className="min-w-[300px] flex-shrink-0 bg-gray-50 rounded-xl flex items-center p-6 first:ml-0 ml-4">
                        <div>
                          <div className="text-lg font-semibold text-gray-800">{liveEvent.subject}</div>
                          <div className="text-base text-gray-600">{liveEvent.topic}</div>
                          {/* Status with colored dot */}
                          <span className="flex items-center gap-1 mt-1">
                            <span className="inline-block w-3 h-3 rounded-full border border-white flex-shrink-0 mr-2 bg-cyan-500"></span>
                            <span className="text-sm font-semibold text-cyan-600">Live</span>
                          </span>
                        </div>
                      </div>
                    )}
                    {/* Upcoming Event Card */}
                    {upcomingEvent && (
                      <div className="min-w-[300px] flex-shrink-0 bg-gray-50 rounded-xl flex items-center p-6 first:ml-0 ml-4">
                        <div>
                          <div className="text-lg font-semibold text-gray-800">{upcomingEvent.subject}</div>
                          <div className="text-base text-gray-600">{upcomingEvent.topic}</div>
                          {/* Status with colored dot */}
                          <span className="flex items-center gap-1 mt-1">
                            <span className="inline-block w-3 h-3 rounded-full border border-white flex-shrink-0 mr-2 bg-orange-400"></span>
                            <span className="text-sm font-semibold text-orange-600">Upcoming</span>
                          </span>
                        </div>
                      </div>
                    )}
                  </div>
                </div>

                <header className="flex items-center justify-between mb-2">
                    <h1 className="text-xl font-bold text-gray-900">Weekly Performance</h1>
                </header>
                <p className="text-gray-500 mb-6">Your progress for this week...</p>
                <div className="mb-10 border rounded-lg">
                    <WeeklyTaskSummary weeklyData={weeklyTaskData} />
                </div>

                <header className="flex items-center justify-between mb-2">
                    <h2 className="text-xl font-bold text-gray-900">Subject Progress</h2>
                </header>
                <p className="text-gray-500 mb-6">Track completed tests for a specific subject.</p>
                <div className="mb-6">
                    <label htmlFor="select-timetable-subject" className="block text-sm font-medium text-gray-700 mb-3">Select Subject:</label>
                    <select
                        id="select-timetable-subject"
                        className="w-full px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
                        value={currentTimetableSubject}
                        onChange={(e) => {
                            const newSubject = e.target.value;
                            setCurrentTimetableSubject(newSubject);
                            setCurrentSelectedSubject(newSubject);
                            localStorage.setItem('currentTimetableSubject', newSubject);
                        }}
                        disabled={subjects.length === 0}
                    >
                        {subjects.length === 0 && <option value="">No Subjects Available</option>}
                        {subjects.map(subject => <option key={subject} value={subject}>{subject}</option>)}
                    </select>
                </div>
                <div id="timetable-progress-card" onClick={() => navigateToTrophyPage(currentTimetableSubject)} className="bg-green-50 rounded-xl p-5 flex items-center justify-between mb-10 cursor-pointer transition-colors duration-300">
                    <CircularProgressBar percentage={progressPercentage} />
                    <div className="flex-1 ml-4">
                        <p className="text-sm text-gray-500">Subject</p>
                        <p id="timetable-progress-subject" className="text-xl font-bold text-gray-800">{currentTimetableSubject || 'Select Subject'}</p>
                        <p className="text-sm text-gray-500 mt-1">Tests Completed</p>
                        <p className="text-lg font-bold text-gray-800">{completedCount}/{totalCount}</p>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><polyline points="9 18 15 12 9 6"></polyline></svg>
                </div>
                
                <header className="flex items-center justify-between mb-2">
                    <h2 className="text-xl font-bold text-gray-900">Today's Focus</h2>
                    <button onClick={() => setIsEditFocusModalOpen(true)} className="bg-blue-50 text-blue-700 font-semibold text-xs px-4 py-2 rounded-full flex items-center hover:bg-blue-100 transition duration-200">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="mr-1"><path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path><path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path></svg>
                        Edit Focus
                    </button>
                </header>
                <p className="text-gray-500 mb-6">
                {todaysFocusTests.length === 0
                        ? "You have no tests in Today's Focus."
                        : `You have ${todaysFocusTests.length} test${todaysFocusTests.length > 1 ? 's' : ''} in Today's Focus.`}
                </p>
                <div id="today-focus-container" className="flex flex-col space-y-4 pb-4 min-w-0">
                    {isLoadingTodayFocus ? (
                        <LoadingSpinner />
                    ) : (
                        todaysFocusTests.length === 0 ? (
                            <p className="text-gray-500 text-center w-full">No focus items for today.</p>
                        ) : (
                            <DndContext
                                sensors={sensors}
                                collisionDetection={closestCenter}
                                onDragEnd={handleDragEnd}
                            >
                                <SortableContext
                                    items={todaysFocusTests.map(item => item.id)}
                                    strategy={verticalListSortingStrategy}
                                >
                                    <AnimatePresence>
                                        {todaysFocusTests.map((item, index) => (
                                            <motion.div key={item.id} layout className="min-w-0">
                                                <FocusItemCard 
    item={item} 
    onRemove={handleRemoveFocusItem} 
    handleToggleTestCompletion={handleToggleTestCompletion} 
    onCheck={handleCheck} 
    isFirst={index === 0} 
    isLast={index === todaysFocusTests.length - 1} 
/>
                                            </motion.div>
                                        ))}
                                    </AnimatePresence>
                                </SortableContext>
                            </DndContext>
                        )
                    )}
                </div>
            </div>

            <EditFocusModal
                isOpen={isEditFocusModalOpen}
                onClose={() => setIsEditFocusModalOpen(false)}
                testData={testData}
                todayFocusItems={todayFocusItems}
                addFocusItem={handleAddFocusItem}
                removeFocusItem={handleRemoveFocusItem}
            />
        </>
    );
});

export default DashboardPage;